import { nanoid } from "@reduxjs/toolkit";

import { TFigure, TFigureArrow, TFigureFrame, TFigureText } from "@brz/ui/src/components/figures";


import { TAppThunk } from "../../types";

import * as actions from '../actions';

type TFields = 'id';

export type TPostFigure = Omit<TFigureFrame, TFields> | Omit<TFigureArrow, TFields> | Omit<TFigureText, TFields>;

export const addFigureThunk = (postFigure: TPostFigure): TAppThunk<string> => (dispatch, getState) => {
    const id = nanoid();

    const figure: TFigure = {
        ...postFigure,
        id,
    };

    dispatch(actions.addFigure(figure));

    return id;
};

export default addFigureThunk;
