import React from 'react';

import Header from '../Header';
import Footer from '../Footer';

import styles from './style.module.css';

type TLayoutBg = 'light' | 'dark' | 'normal';

type TProps = {
    bg?: TLayoutBg;
    top?: React.ReactNode;
    bottom?: React.ReactNode;
    children?: React.ReactNode;
};

const LayoutBoard = (props: TProps) => {
    const {
        bg = 'normal',
        children,
    } = props;

    const rootClassName = styles.Layout + ' ' + styles[`Layout--${bg}`];

    return (
        <div className={rootClassName}>
            <div className={styles.Layout__top}>
                <Header />
            </div>
            {!!children && (
                <div className={styles.Layout__body}>
                    {children}
                </div>
            )}
            <div className={styles.Layout__bottom}>
                <Footer />
            </div>
        </div>
    );
};

export default LayoutBoard;