import { useCallback } from 'react';

import { TFigure } from '@brz/ui/src/components/figures';
import { TPostFigure } from '../state/draw/operations/addFigureThunk';

import * as drawSelectors from '../state/draw/selectors';
import * as drawActions from '../state/draw/actions';
import * as drawOperations from '../state/draw/operations';

import useAppSelector from './useAppSelector';
import useAppDispatch from './useAppDispatch';

const useFigures = () => {
    const figures = useAppSelector(drawSelectors.figures);

    const dispatch = useAppDispatch();

    const updatefigure = useCallback((figure: TFigure) => {
        dispatch(drawActions.updatefigure({ figure }));
    }, [
        dispatch,
    ]);

    const removeFigure = useCallback((figureId: string) => {
        dispatch(drawActions.removeFigure(figureId));
    }, [
        dispatch,
    ]);

    const addFigure = useCallback((figure: TPostFigure) => {
        const id = dispatch(drawOperations.addFigureThunk(figure));
        return id;
    }, [
        dispatch,
    ]);

    return {
        figures,
        updatefigure,
        removeFigure,
        addFigure,
    };
};

export default useFigures;
