import React, { useEffect, useRef, useCallback, CSSProperties, useMemo } from "react";

import { WATER_MARK } from "../../config";

import { htmlToImagePngBlob } from "@brz/ui";

import Figure from "../Figure";

import useFigures from "../../hooks/useFigures";
import useImg from "../../hooks/useImg";

import styles from './style.module.css';

type TProps = {
    onReady: (imagePngBlob: Blob) => void;
    onError: (error: unknown) => void;
};

const CombineBoard = (props: TProps) => {
    const {
        onReady,
        onError,
    } = props;

    const ref = useRef<HTMLDivElement | null>(null);

    const combineToBlob = useCallback(async () => {
        try {
            const div = ref.current;
            if (!div) {
                throw Error('Что-то пошло не так!');
            }

            const imagePngBlob = await htmlToImagePngBlob(div);
            onReady(imagePngBlob);

        } catch (error) {
            onError(error);
        }
    }, [
        onReady,
        onError,
    ]);

    useEffect(() => {
        combineToBlob();
    }, [
        combineToBlob,
    ]);

    const { figures } = useFigures();
    const img = useImg();

    const style = useMemo(() => {
        if (img) {
            const {
                width,
                height,
                dataUrl,
            } = img;

            const watermarHeight = 24;

            const style: CSSProperties = {
                width,
                height: height + watermarHeight,
                backgroundImage: `url(${dataUrl})`,
                backgroundPosition: '0 0',
                position: 'relative',
            };
            return style;
        }

        return {};
    }, [
        img,
    ]);

    return (
        <div className={styles.root}>
            <div
                style={style}
                ref={ref}
            >
                {figures.map((figure) => (
                    <Figure
                        key={figure.id}
                        figure={figure}
                    />
                ))}
                <div className={`${styles.watermar} ${styles['watermar--bottom']}`}>
                    {new Array(15).fill(1).map(() => {
                        return WATER_MARK + ' ';
                    })}
                </div>
            </div>
        </div>
    );
};

export default CombineBoard;
