import { useCallback, useEffect, useMemo } from "react";

import { TXY } from "@brz/ui/src/components/FloatPanel/types";

import * as panelActions from '../state/panel/actions';
import * as panelSelectors from '../state/panel/selectors';

import { TViewport, useViewport } from "@brz/ui";
import useAppDispatch from "./useAppDispatch";
import useAppSelector from "./useAppSelector";

type TUse = () => [
    panelXY: TXY,
    setPanelXY: (panelXY: TXY) => void,
];

const MIN_SIZE = 24;

const usePanelXY: TUse = () => {
    const dispatch = useAppDispatch();

    const viewport = useViewport();
    const panelXYSelector = useAppSelector(panelSelectors.panelXY);

    const panelXY: TXY = useMemo(() => {
        if (panelXYSelector) {
            return panelXYSelector;
        }
        const [vw] = viewport;
        return [vw - 350, 50];
    }, [
        panelXYSelector,
        viewport,
    ]);

    const getPanelXY = useCallback((vp: TViewport, xy: TXY) => {
        let bool = false;

        const [vw, vh] = vp;
        let [x, y] = xy;

        if (x < 0) {
            x = 0;
            bool = true;
        }

        if (x > (vw - MIN_SIZE)) {
            x = vw - MIN_SIZE;
            bool = true;
        }

        if (y < 0) {
            y = 0;
            bool = true;
        }

        if (y > (vh - MIN_SIZE)) {
            y = vh - MIN_SIZE;
            bool = true;
        }

        const returnXy: TXY = [x, y];
        return {
            bool,
            xy: returnXy,
        };
    }, []);

    const setPanelXY = useCallback((newPanelXY: TXY) => {
        const { xy } = getPanelXY(viewport, newPanelXY);
        dispatch(panelActions.setPanelXY({ panelXY: xy }));
    }, [
        dispatch,
        getPanelXY,
        viewport,
    ]);

    const setPanelXYByViewport = useCallback((newViewport: TViewport) => {
        const { xy, bool } = getPanelXY(newViewport, panelXY);
        if (bool) {
            dispatch(panelActions.setPanelXY({ panelXY: xy }));
        }
    }, [
        dispatch,
        getPanelXY,
        panelXY,
    ]);

    useEffect(() => {
        setPanelXYByViewport(viewport);
    }, [
        viewport,
        setPanelXYByViewport,
    ]);

    return [
        panelXY,
        setPanelXY,
    ];
};

export default usePanelXY;
