import React from 'react';

import { FloatPanel } from '@brz/ui';

import usePanelXY from '../../hooks/usePanelXY';

import styles from './style.module.css';

type TProps = {
    children: React.ReactNode,
};

const Panel = (props: TProps) => {
    const {
        children,
    } = props;

    const [panelXY, setPanelXY] = usePanelXY();

    return (
        <FloatPanel
            panelXY={panelXY}
            setPanelXY={setPanelXY}
        >
            <div className={styles.root}>
                {children}
            </div>
        </FloatPanel>
    );
};

export const PanelSection = (props: TProps) => (
    <div className={styles.section}>
        {props.children}
    </div>
);

export const PanelRow = (props: TProps) => (
    <div className={styles.row}>
        {props.children}
    </div>
);

export default Panel;
