import React from 'react';

import { FigureArrow, FigureFC, FigureFrame, FigureText, TFigure } from '@brz/ui/src/components/figures';

const Figure: FigureFC<TFigure> = (props) => {
    const {
        figure,
        showHover,
        onPointerDown,
    } = props;

    if (figure.name === 'text') {
        return (
            <FigureText
                figure={figure}
                onPointerDown={onPointerDown}
                showHover={showHover}
            />
        );
    }

    if (figure.name === 'arrow') {
        return (
            <FigureArrow
                figure={figure}
                showHover={showHover}
                onPointerDown={onPointerDown}
            />
        );
    }

    if (figure.name === 'frame') {
        return (
            <FigureFrame
                figure={figure}
                showHover={showHover}
                onPointerDown={onPointerDown}
            />
        );
    }

    return null;
};

export default Figure;
